/* Tech Stack / Work Page Specific Styles */

/* Tech stack section with enhanced visuals */
.stack {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-bottom: 24px;
  padding: 20px;
  background: linear-gradient(135deg, #f5f5f5 0%, #e8e8e8 100%);
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.1);
}

p.work {
  font-size: 12px;
  color: #333333;
  line-height: 1.6;
}

/* Tech stack icons with hover effects */
.stack img {
  width: 50px;
  height: 50px;
  padding: 8px;
  margin: 4px;
  background: linear-gradient(180deg, #ffffff 0%, #f0f0f0 100%);
  border: 2px solid;
  border-color: #ffffff #808080 #808080 #ffffff;
  filter: drop-shadow(-1px 1px 2px rgba(0, 0, 0, 0.2));
  transition: all 0.3s;
  cursor: pointer;
}

.stack img:hover {
  width: 60px;
  height: 60px;
  padding: 8px;
  margin: -1px;
  background: linear-gradient(180deg, #f0f8ff 0%, #e0f0ff 100%);
  transform: translateY(-4px) rotate(5deg);
  filter: drop-shadow(-2px 4px 4px rgba(0, 0, 0, 0.3));
  z-index: 10;
  position: relative;
}

.tech-item {
  text-align: center;
}

.category-tabs {
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  flex-direction: row;
  align-content: center;
  align-items: center;
  gap: 10px;
}

/* Other work section */
.other-work {
  margin-top: 24px;
  padding: 16px;
  background: linear-gradient(180deg, #fafafa 0%, #f0f0f0 100%);
  border: 2px dashed #808080;
}

.other-work p {
  font-size: 12px;
  line-height: 1.8;
}

.other-work p * {
  vertical-align: middle;
}

/* Responsive adjustments */
@media screen and (max-width: 540px) {
  .projects {
    min-height: 200px;
    margin: 20px 0;
  }

  .old-projects.welcome ul {
    padding-left: 10px;
  }

  .stack img {
    width: 40px;
    height: 40px;
    padding: 6px;
  }

  .stack img:hover {
    width: 50px;
    height: 50px;
  }
}
